import { writable, derived } from "svelte/store";
;
const createThemeSettings = function createSettingsStore() {
    const { subscribe, set, update } = writable({
        isDark: false
    });
    return {
        subscribe,
        toggleDarkMode: () => update((state) => {
            state.isDark = !state.isDark;
            return state;
        }),
        loadMode: (isDarkTheme) => update((state) => {
            state.isDark = isDarkTheme;
            return state;
        })
    };
};
export const themeSettings = createThemeSettings();
export const darkIsActive = derived([themeSettings], ([$themeSettings]) => {
    return $themeSettings.isDark;
});
