<script lang="ts">
	import {themeSettings, darkIsActive} from "../../../stores/SettingsStore"
	import {Light, Dark} from "./svgs";
	import {onMount} from "svelte";
	
	$: comp = $darkIsActive ? Light : Dark;

	function toggleDarkMode() {
		themeSettings.toggleDarkMode();
		window.localStorage.setItem("isDarkTheme", `${$darkIsActive}`);
	}

	onMount(() => {
		const isDarkTheme = window.localStorage.getItem("isDarkTheme");

		if (isDarkTheme) {
			themeSettings.loadMode(isDarkTheme === "true");

		// 	document.documentElement.setAttribute(
		// 	"data-theme",
		// 	$darkIsActive ? "dark" : "light"
		// );
		}
	});

	$: {
		document.documentElement.setAttribute(
			"data-theme",
			$darkIsActive ? "dark" : "light"
		);
	}
</script>

<div class="parent" on:click={toggleDarkMode}>
	<div class="item" title="toggle dark mode">
		<svelte:component this={comp} />
	</div>
</div>

